<template>
    <div class="row">
            <div class="col-3">
                <div class="card card-secondary">
                    <div class="card-header text-center">
                        <h3 class="card-title">
                            Requested
                        </h3>
                    </div>

                    <div class="card-body">
                        <template v-for="request in requestedStatusRequests">
                            <KanbanCard :req="request"/>
                        </template>
                    </div>
                </div>
            </div>
            <div class="col-3">
                <div class="card card-primary">
                    <div class="card-header text-center">
                        <h3 class="card-title">
                            Processing
                        </h3>
                    </div>

                    <div class="card-body">
                        <template v-for="request in processingStatusRequests">
                            <KanbanCard :req="request"/>
                        </template>
                    </div>
                </div>
            </div>

            <div class="col-3">
                <div class="card card-default">
                    <div class="card-header text-center bg-info">
                        <h3 class="card-title">
                            In progress
                        </h3>
                    </div>

                    <div class="card-body">
                        <template v-for="request in inProgressStatusRequests">
                            <KanbanCard :req="request"/>
                        </template>
                    </div>
                </div>
            </div>

            <div class="col-3 scroll">
                <div class="card card-success">
                    <div class="card-header text-center">
                        <h3 class="card-title">
                            Done
                        </h3>
                    </div>

                    <div class="card-body">
                        <template v-for="request in doneStatusRequests">
                            <KanbanCard :req="request"/>
                        </template>
                    </div>
                </div>
            </div>
        </div>
</template>
<script>
import KanbanCard from './KanbanCard.vue';
import Navbar from './Navbar.vue';

export default {
    components: {
    KanbanCard,
    Navbar
},
    data () {
        return {
            requests: [],
            requestedStatusRequests: [],
            processingStatusRequests: [],
            inProgressStatusRequests: [],
            doneStatusRequests: []
        }
    },
    mounted () {
        this.getRequestsData();
    },
    methods: {
        getRequestsData() {
            google.script.run.withSuccessHandler(this.onSuccessRequest).getRequestsData();
        },
        onSuccessRequest(data) {
            this.requests = JSON.parse(data);

            this.requestedStatusRequests = this.requests.filter((value) => {
                return value.requestStatus == 'Requested' || value.requestStatus == 'requested';
            });

            this.processingStatusRequests = this.requests.filter((value) => {
                return value.requestStatus == 'processing';
            });

            this.inProgressStatusRequests = this.requests.filter((value) => {
                return value.requestStatus == 'InProgress';
            });

            this.doneStatusRequests = this.requests.filter((value) => {
                return value.requestStatus == 'Done';
            });
        },
    }
}
</script>

<style>
.scrollable{
  overflow-y: auto;
}
</style>